<template>
    <div>
        <div class="logo" @click="toHome"><div class="container"><a href="javascript:;"></a></div></div>
        <div class="panel">
            <div class="container">
                <div class="panel-layout">
                    <div class="panel-tabs">
                        <a href="javascript:void(0);" class="tabs-link" :class={select:isLoginEnter} data-tab="login" @click="loginAc(true)">账号登录</a>
                        <span class="line"></span>
                        <a href="javascript:void(0);" class="tabs-link" :class={select:!isLoginEnter} @click="loginAc(false)" data-tab="register">账号注册</a>
                    </div>
                    <div class="panel-account">
                        <form action="javascript:;" method="POST" id="form" @submit="submit(isLoginEnter)">
                            <label class="account-label" >
                                <input
                                        class="account-item"
                                        autocomplete="off"
                                        type="text"
                                        placeholder="账号"
                                        v-model="form.username"
                                        v-validate="'required'"
                                        name="name"
                                        data-vv-as="用户名"
                                        :class="{errBorder:errors.has('name')}"
                                >
                            </label>
                            <span class="error">{{ errors.first('name') }}</span>
                            <label class="account-label" >
                                <input
                                        class="account-item"
                                        type="password"
                                        placeholder="密码"
                                        autocomplete="off"
                                        name="password"
                                        v-model="form.password"
                                        v-validate="'required'"
                                        data-vv-as="密码"
                                        :class="{'is-danger': errors.has('password')}"
                                        ref="password"
                                >
                            </label>
                            <span class="error">{{ errors.first('password') }}</span>
                            <label class="account-label password2" v-if="!isLoginEnter">
                             <input label="确认密码"
                                    placeholder="请再次输入密码"
                                    v-validate="'required|confirmed:password'"
                                    data-vv-as="确认密码"
                                    data-vv-name="repassword"
                                    :class="{'is-danger' :errors.has('repassword') }"
                                       v-model="form.repassword"
                                       class="account-item"
                                >
                            </label>
                             <span v-show="errors.has('repassword')" class="error">{{ errors.first('repassword') }}</span>
                            <input v-if="isLoginEnter"
                                   class="btn"
                                   type="submit"
                                   :value="'登录'"
                                   :disabled="(errors.has('password')||errors.has('name'))?true:false"
                                   :class="{dis:(errors.has('password')||errors.has('name'))}">
                            <input v-if="!isLoginEnter"
                                   class="btn"
                                   type="submit"
                                   :value="'注册'"
                                   :disabled="(errors.has('password')||errors.has('name')||errors.has('repassword'))?true:false"
                                   :class="{dis:(errors.has('password')||errors.has('name')||errors.has('repassword'))}">
                        </form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import {mapState} from "vuex";
import Vue from "vue"
export default{
    created(){
       if(this.$route.query.isPid){
           this.ifPid=true;
       }
    },
    data(){
        return{
           form:{
               username:'',
               password:'',
               repassword:'',
               ifPid:false,
           },
        }
    },
    watch:{

    },
    computed:{
        ...mapState("userMs",["isLoginEnter"]),

    },
    methods:{
        toHome(){
            this.$router.replace("/");
        },
        loginAc(flag){
            this.$store.dispatch("userMs/loginEnter",flag);
        },
        submit(flag){
            const myform={
                name:'',
                password:""
            };
            myform.name=this.form.username;
            myform.password=this.form.password;
            if(flag){
                this.$store.dispatch("userMs/login",myform).then((ee)=>{
                    Vue.$message({
                        type:"success",
                        message:"登录成功"
                    });
                    this.$store.dispatch("userMs/getShopCart");
                    if(this.ifPid){
                        this.$router.replace({path:"/product",query:{pid:this.$route.query.isPid}});
                    }else {
                        this.$router.replace("/");
                    }
                });
            }else {
                this.$store.dispatch("userMs/register",myform).then(()=>{
                    Vue.$message({
                        type:"success",
                        message:"注册成功,跳转到首页"
                    })
                    this.$store.dispatch("userMs/getShopCart");
                    if(this.ifPid){
                        this.$router.replace({path:"/product",query:{pid:this.$route.query.isPid}});
                    }else {
                        this.$router.replace("/");
                    }
                });
            }

            }
        }

    }
</script>
<style lang="scss" scoped>
    body,a{
        margin: 0;
        padding: 0;
    }
    .error{
        color: red;
    }
    a{
        text-decoration: none;
    }
    .container{
        width: 1130px;
        margin: 0 auto;
    }
    .container::before , .container::after, .clearfix::before, .clearfix::after {
        content: "";
        display: table;
    }
    .container::after, .clearfix::after {
        clear: both;
    }
    .logo a {
        display: block;
        background: url("../img/mistore_logo.png") no-repeat;
        width: 200px;
        height: 98px;
    }
    .panel {
        background: url("../img/loginbg.jpg") no-repeat top center;
        height: 588px;
    }
    .panel-layout{
        width: 410px;
        min-height: 524px;
        background: white;
        margin-top: 32px;
        float: right;
    }


    .panel-tabs {
        padding: 27px 0 24px;
        text-align: center;
        font-size: 24px;
        color: #e0e0e0;
    }
    .panel-tabs .select{
        color: #f56600;
    }
    .panel-tabs .line{
        width: 1px;
        height: 24px;
        margin: 0 35px 0 42px;
        border: 1px solid #e0e0e0;
    }
    .tabs-link {
        padding: 27px 0 24px;
        text-align: center;
        font-size: 24px;
        color: #666;
    }

    .panel-account {
        width: 348px;
        margin: 0 auto;
        margin-top: 10px;
    }
    .account-item {
        width: 306px;
        height: 22px;
        line-height: 22px;
        padding: 13px 16px 13px 14px;
        display: block;
        outline: 0;
        border: 0 none;
        color: #333;
    }
    .account-label{
        margin-bottom: 14px;
        border: 1px solid #e0e0e0;
        outline: 0 none;
        display: -webkit-box;
    }
    .panel-account .btn {
        background-color: #f56600;
        width: 100%;
        height: 50px;
        line-height: 50px;
        display: block;
        margin-bottom: 14px;
        text-align: center;
        font-size: 14px;
        color: #fff;
        cursor: pointer;
        border: 0;
        outline: 0;
    }
    .panel-account .dis{
        cursor: not-allowed;
    }
</style>