<template>
    <div>
        <app-header ></app-header>
        <div class="product">
            <div class="container">
                <div class="pro-view">
                    <div class="view-list">
                        <template  >
                        <img :src="sku_img" >
                        </template>
                    </div>
                    <div class="view-control">
                        <div class="control-lr">
                            <a href="#" class="control-prev">上一张</a>
                            <a href="#" class="control-next">下一张</a>
                        </div>
                        <div class="control-page">
                        </div>
                    </div>
                </div>
                <div class="pro-info">
                    <h1 class="pro-title">{{product.name}}</h1>
                    <p class="pro-desc">{{product.desc}}</p>
                    <div class="pro-price">
                        <span><label class="price">{{price}}</label>元</span>
                    </div>
                    <div class="pro-choose">
                        <div class="choose1">
                            <p class="choose-title">选择<span class="prop-name">{{product.prop1_name}}</span></p>
                            <ul class="choose-list clearfix">
                                <li v-for="(value,index) in product.options" :key="index" @click="changeActive(index,value.list)" :class="{active:activeIndex==index}" >
                                    <a href="javascript:;">
                                        <span class="list-name">{{value.prop1_value}}</span>
                                        <span class="list-price"> <label class="price">{{value.price}}</label>元 </span>
                                    </a>
                                </li>
                            </ul>
                        </div>
                        <div class="choose2" >
                            <p class="choose-title">选择<span class="prop-name">{{product.prop2_name}}</span></p>
                            <ul class="choose-list clearfix">
                                <template v-if="product.options">
                                <li  ref="sku" v-for="(value,index) in product.options[activeIndex].list" :key="index" @click="commit(value,index)" :class="{active:colorActive==index}" :data-id="value.sku_id">
                                    <a href="javascript:;" >
                                        <span class="list-name">{{value.prop2_value}}</span>
                                    </a>
                                </li>
                                </template>
                            </ul>
                        </div>
                    </div>
                    <div class="pro-btn"><a href="javascript:;" id="addCart" @click="addShopCart()">加入购物车</a></div>
                </div>

            </div>

        </div>
        <div></div>
    </div>
</template>
<script>
import {mapState} from "vuex";
import appHeader from "../components/Miheader.vue"
import {api} from "../utils/api"
import Vue from "vue"
import { MessageBox } from 'element-ui';
export default{
    created(){
        document.title=this.product.name;
       this.$store.dispatch("render/getIsHome",false);
      if(this.$route.query.pid){
          const pid="pid="+this.$route.query.pid;
          this.isPid=this.$route.query.pid;
              this.$store.dispatch("productDetail/getProduct",pid).then(()=>{
                  if(this.product.options){
                      this.sku_id=this.product.options[this.activeIndex].list[this.colorActive].sku_id;
                      this.sku_img=this.product.options[this.activeIndex].list[this.colorActive].imgs[0];
                      this.price=this.product.options[this.activeIndex].list[this.colorActive].price;
                  }
              });
        }
          },
    data(){
        return{
            activeIndex:0,
            colorActive:0,
            sku_img:[],
            sku_id:0,
            price:1499,
            isPid:0
          }
        },
        components:{
            appHeader
        },
        computed:{
            ...mapState("userMs",['isLogin']),
            ...mapState("render",["carousel"]),
            ...mapState("productDetail",["product"]),
        },
    methods:{
        changeActive(index,list){
            this.colorActive=0;
            this.activeIndex=+index;
            this.sku_img=list[this.colorActive].imgs[0];
            this.price=list[this.colorActive].price;

        },
        commit(val,index){
            this.sku_id=val.sku_id;
            this.sku_img=val.imgs;
           this.colorActive=index;
        },

        addShopCart(){
            const data=this.$refs.sku;
            let id=0;
            for(let i=0;i<data.length;i++){
                if(data[i].getAttribute("class")==='active'){
                    id=data[i].getAttribute("data-id")
                }
            }
            if(this.isLogin){
                api.post(`/user/addCart`,{sku_id:id}).then(()=>{
                        Vue.$message({
                            type:"success",
                            message:"添加成功"
                        });
                    this.$store.dispatch("userMs/getShopCart").then(()=>{
                    })
                })
            }else {
                const that=this;
                MessageBox.confirm('是否前往登录', '您尚未登录', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning',
                    center: true
                }).then(() => {
                    that.$router.replace({path:'/login',query:{isPid:this.isPid}})
                }).catch(() => {

                });
            }

        }
    }
    }
</script>
<style lang="scss" scoped>
    @import "../css/style.css";
    @import "../css/iconfont.css";
    @import "../css/product.css";

</style>